import React, { Component } from "react";
import PropTypes from "prop-types";

import st from "./style";
import DefaultButton from '../../ui/DefaultButton';
import CakeTahTiti from '../../images/CakeTahTiti';
import Colors from '../../style/Colors';

class OverlayError extends Component {
  
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {

    return (
      <div style={st.container(this.props)}>
        <div style={st.ctnImage}>
          <CakeTahTiti />
        </div>
        <h1 style={st.number}>{this.props.number}</h1>
        <h3 style={st.title}>{this.props.title}</h3>
        <p  style={st.text}>{this.props.text}</p>
        <DefaultButton
          label={this.props.labelButton}
          onClick={this.props.onClickButton}
        />
      </div>
    );
  }
}

OverlayError.propTypes = {
  number:PropTypes.number,
  title:PropTypes.string,
  text:PropTypes.string,
  labelButton:PropTypes.string,
  onClickButton:PropTypes.func,
  bgOpacity:PropTypes.number,
  bgColor:PropTypes.string,
};
OverlayError.defaultProps = {
  number:null,
  title:"",
  text:",",
  labelButton:"Tente novamente",
  onClickButton:()=>{},
  bgOpacity:1,
  bgColor:Colors.lightGrey20,
};

export default OverlayError;
